import axios, { AxiosError, AxiosResponse } from 'axios'
import { toast } from 'react-toastify'
import { PaginatedResponse } from '../models/pagination'
import { router } from '../router/Routes'
import { store } from '../store/configureStore'

const sleep = () => new Promise((resolve) => setTimeout(resolve, 500))

axios.defaults.baseURL = process.env.REACT_APP_API_URL
axios.defaults.withCredentials = true

const responseBody = (response: AxiosResponse) => response.data

axios.interceptors.request.use((config) => {
  const token = store.getState().account.user?.token
  if (token) config.headers.Authorization = `Bearer ${token}`
  return config
})

axios.interceptors.response.use(
  async (response) => {
    if (process.env.NODE_ENV === 'development') await sleep()
    const pagination = response.headers['pagination']
    if (pagination) {
      response.data = new PaginatedResponse(response.data, JSON.parse(pagination))
      return response
    }
    return response
  },
  (error: AxiosError) => {
    const { data, status } = error.response as AxiosResponse
    switch (status) {
      case 400:
        if (data.errors) {
          const modelStateErrors: string[] = []
          for (const key in data.errors) {
            if (data.errors[key]) {
              modelStateErrors.push(data.errors[key])
            }
          }
          throw modelStateErrors.flat()
        }
        toast.error(data.title)
        break
      case 401:
        toast.error(data.title || 'Unauthorised')
        break
      case 403:
        toast.error('You are not allowed to do that!')
        break
      case 405:
        toast.error(data.title)
        break
      case 500:
        router.navigate('/server-error', { state: { error: data } })
        break
      default:
        break
    }
    return Promise.reject(error.response)
  },
)

const requests = {
  get: (url: string, params?: URLSearchParams) => axios.get(url, { params }).then(responseBody),
  post: (url: string, body: {}) => axios.post(url, body).then(responseBody),
  put: (url: string, body: {}) => axios.put(url).then(responseBody),
  delete: (url: string) => axios.delete(url).then(responseBody),
  postForm: (url: string, data: FormData) =>
    axios
      .post(url, data, {
        headers: { 'Content-type': 'multipart/form-data' },
      })
      .then(responseBody),
  putForm: (url: string, data: FormData) =>
    axios
      .put(url, data, {
        headers: { 'Content-type': 'multipart/form-data' },
      })
      .then(responseBody),
}

function createFormData(item: any) {
  let formData = new FormData()
  for (const key in item) {
    formData.append(key, item[key])
  }
  return formData
}

const Admin = {
  createProduct: (product: any) => requests.postForm('products', createFormData(product)),
  updateProduct: (product: any) => requests.putForm('products', createFormData(product)),
  deleteProduct: (id: number) => requests.delete(`products/${id}`),
  createConfig: (config: any) => requests.postForm('config', createFormData(config)),
  updateConfig: (config: any) => requests.putForm('config', createFormData(config)),
  removeConfig: (id: any) => requests.delete(`config/${id}`), 
  getConfigPresets: () => requests.get('config/getConfigPresets'),
  addConfigPreset: (configPreset: any, productId: number) => requests.post(`config/AddConfigPresetComposition/${productId}`, configPreset)
}
  

const Catalog = {
  list: (params: URLSearchParams) => requests.get('products', params),
  details: (id: number) => requests.get(`products/${id}`),
  fetchFilters: () => requests.get('products/filters'),
}
const promiseAndCatchError = (path: string) => {
  requests.get(path).catch((error) => console.log(error))
}
const TestErrors = {
  get400Error: () => promiseAndCatchError('buggy/bad-request'),
  get401Error: () => promiseAndCatchError('buggy/unauthorised'),
  get404Error: () => promiseAndCatchError('buggy/not-found'),
  get500Error: () => promiseAndCatchError('buggy/server-error'),
  getValidationerror: () => requests.get('buggy/validation-error'),
}

const productReq = (productId: number, quantity: number, cfgId?: number) => {
  let str = `basket?productId=${productId}&quantity=${quantity}`
  if (cfgId) str += `&configId=${cfgId}`
  return str
}

const Basket = {
  get: () => requests.get('basket'),
  addItem: (productId: number, quantity = 1, cfgId?: number) =>
    requests.post(productReq(productId, quantity, cfgId), {}),
  removeItem: (productId: number, quantity = 1, cfgId?: number) =>
    requests.delete(productReq(productId, quantity, cfgId)),
}

const Account = {
  login: (values: any) => requests.post('account/login', values),
  register: (values: any) => requests.post('account/register', values),
  currentUser: () => requests.get('account/currentUser'),
  fetchAddress: () => requests.get('account/savedAddress'),
}

const Orders = {
  list: () => requests.get('orders'),
  fetch: (id: number) => requests.get(`/orders/${id}`),
  create: (values: any) => requests.post('orders', values),
}

const Payments = {
  createPaymentIntent: () => requests.post('payments', {}),
}
const agent = {
  Catalog,
  TestErrors,
  Basket,
  Account,
  Orders,
  Payments,
  Admin,
}

export default agent
